    monica_1: "Our desire to translate Mónica de la Torre's work arose from a chance encounter in New York. This translation, constructed during the fall of 2019, was a collaborative effort both between us as members of the collective and with the poet herself.",
    monica_2: "Mónica de la Torre's work spans both English and Spanish; even so, her books have yet to be translated into French. Her first introduction to a French-language readership came through a reading she gave in Paris with the bilingual reading series Double Change – for this occasion, Olivier Brossard translated a few poems from her latest book, Repetition 19.",
    monica_3: "The hybridity of de la Torre's work is inspired by a range of artistic practices, from fine arts to video to performance art. Sitting at the intersection of languages and cultures, she plays with the materiality of language and the fractal possibilities of code-switching and the cross-linguistic encounter. Central to The Happy End/All Welcome is the motif of the chair in its many incarnations. Unassuming at first, the various chairs of the book crystallize a range of poetic intentions: ekphrastic references to design become ironic evocations of corporate, late-capitalist culture. De la Torre's poetic voice in this collection oscillates between several universes: art criticism, startup lingo, and management jargon co-mingle; their humorous juxtaposition makes their absurdity all the more visible.",
    monica_4: "Funny and grating, The Happy End/All Welcome puts up a twisted mirror to a corporate culture anchored in the US, but now globalized to no end. We are excited to share this work with French readers, who will no doubt recognize the language of startups and afterworks, English borrowings and strange turns of phrase.",
    eleni_1: "Due to her love of France and her position teaching creative writing in Providence, RI, we have had the good fortune to encounter Eleni Sikelianos time and time again. Her enthusiasm, as well as the ecological and feminist dimensions of her work, convinced us to translate, with her consent, her latest book, What I Knew.",
    eleni_2: "The connections between Sikelianos and France are many: she regularly visits for readings (in the Double Change reading series, in the festival Littérature en bords de mer, or Littérature au Centre in the spring of 2021), and several of her books have already been translated into French (e.g., The California Poem, You Animal Machine).",
    eleni_3: "In What I Knew, Sikelianos returns to the epic structure that characterized The California Poem, offering a new reflection on the ephemeral qualities of knowledge. She enumerates various and disparate memories, quotations she has read or heard by way of books and paths, her reflections on ecological devastation and her own desires. Behind her powerful voice lie numerous others, behind which she sometimes hides, or which she uses as mouthpieces, giving witness to natural landscapes and especially to their undoing.",
    eleni_4: "Our proposal of a French translation of What I Knew seems, in this context, especially natural – given the presence of Sikelianos's work in French-language libraries and her profound ability to speak to our present times.",
    laura_1: "Our translation of Laura Vazquez's text arose from Lénaïg's discovery of the text at the L'Hydre bookstore in the mille têtes neighborhood of Marseille, just before quarantine began. With the poet's consent, Shira and Lénaïg began whittling away at a new translation during that strange time...",
    laura_2: "Laura Vazquez is a poet from the south of France who, over the past several years, has developed a broad and varied body of work, from poetry to performance by way of song (with the band Tsuku) and the literary journal Muscle.",
    laura_3: "In La Main de la Main, Vazquez deftly weaves a subaltern world at the intersection of the poetic and the grotesque. Where is the line between our everyday troubles, our things, and the ecosystem in which we live? Vazquez's language, deceptively simple at first glance, exposes the subtle cracks in everyday speech to reveal their uncanny interior.",
