    monica_1: "C’est une rencontre avec Mónica de la Torre à New York qui a donné naissance au désir de traduire son oeuvre. Notre traduction s’est construite collectivement et en dialogue avec la poète au cours de l’automne 2019.",
    monica_2: "L’oeuvre de Mónica de la Torre se construit parallèlement en espagnol et en anglais, elle n’a toutefois pas encore été traduite en français ; seuls quelques poèmes ont été traduits par Olivier Brossard à l’occasion d’une lecture qu’elle a donnée à Paris le 4 Juillet 2019 (dans le cadre de la série de lectures bilingues Double Change).",
    monica_3: "L’oeuvre hybride de Mónica de la Torre s’inspire de productions artistiques diverses, des arts décoratifs à la vidéo en passant par la performance. À l’intersection entre deux cultures, elle joue avec la matérialité de la langue et les possibilités polysémiques de la rencontre entre divers vocabulaires et contextes d’expression. Au centre de The Happy End / All Welcome, le motif du fauteuil, à première vue anodin, cristallise diverses intentions poétiques : la prégnance de la référence au design (le texte est truffé d’ ekphraseis ), et l’évocation ironique au monde du travail en entreprise (le fauteuil est tantôt celui du cadre à sa table de travail, tantôt celui de l’entretien d’embauche). La langue poétique de Mónica de la Torre dans ce recueil oscille entre plusieurs univers : celui de la critique d’art, de la publicité et du monde managérial ; la poète s’en empare avec humour et les juxtapose de manière hétéroclite, pour mettre en évidence leur étrangeté et leur absurdité. En ressort un portrait grinçant du monde capitaliste et productiviste.",
