    eleni_2: "Les liens entre Eleni Sikelianos et la France sont étroits et anciens : elle s’y rend régulièrement pour des lectures (dans le cadre de la série Double Change, du festival Littérature en bords de mer, ou encore Littérature au Centre au printemps 2021), et plusieurs de ses livres y ont déjà été traduits (Le poème Californie, Animale Machine).",
    eleni_3: "Dans What I knew, Eleni Sikelianos renoue avec la veine épique qui caractérisait déjàThe California Poem, pour proposer une réflexion nouvelle sur le caractère éphémère de la connaissance. Elle y énumère ses souvenirs hétéroclites et disparates, les paroles qu’elle a lues ou entendues, au hasard des livres et des chemins, ses réflexions sur l’urgence écologique, ses désirs de femme. Derrière la voix, puissante, de la poète, s’élève donc une multiplicité d’autres voix, d’autres êtres, derrière qui elle s’efface parfois, ou dont elle se fait la porte- parole, attestant du paysage d’une époque, et tout particulièrement des dérèglements de celle-là.",
