    laura_3: "Dans La main de la main, la poète explore un monde charnel et subtil, à la fois simple et poétique. Il s’agit, notamment, de dire la souffrance du quotidien et du rapport au monde par le biais des objets qui nous entourent. La langue de Laura Vazquez, si elle exhibe une apparente simplicité dans le choix des tournures syntaxiques et du vocabulaire, explore les espaces de disjonctions que créent de légers et discrets décalages par rapport à l’usage commun – de cette recherche naît un effet d’étrangeté qui se superpose, à la lecture, à l’apparente simplicité de ses poèmes.",
